* {
  box-sizing: border-box;
}

body {
  font-family: 'Open Sans', 'Helvetica Neue', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #fafafa;
  color: #212121;
  line-height: 1.5;
  margin: 0;
}

.comments {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  grid-gap: 20px;
  padding: 20px;
}

.comments .item {
  box-shadow: 0 0 5px 1px rgba(0, 0, 0, 0.25);
  padding: 15px;
  border-radius: 3px;
  position: relative;
  display: flex;
  flex-direction: column;
}

.comments .item .delete {
  position: absolute;
  top: 10px;
  right: 10px;
  width: 20px;
  height: 20px;
  padding: 0;
  cursor: pointer;
}

.comment-container {
  background-color: #f2f0fa;
  padding: 20px 20px 0;
}

.comment-form,
.toggle-container {
  max-width: 400px;
  margin: 0 auto;
}

.comment-form label {
  padding-left: 15px;
  margin-bottom: 5px;
  display: block;
}

.comment-form input,
.comment-form button {
  width: 100%;
  margin-bottom: 20px;
  line-height: 40px;
  border: 0 none;
  border-radius: 25px;
  text-indent: 15px;
  font-size: 16px;
}
.comment-form button {
  background-color: blue;
  color: #fff;
}

.data {
  font-size: 12px;
  color: #9b9b9b;
  margin-top: auto;
  text-align: right;
}

.author {
  text-align: center;
  font-weight: 600;
  margin-bottom: 5px;
}

.toggle-container {
  padding: 20px 0;
}
.toggle-comments {
  margin: 0 auto;
  width: 100%;
  line-height: 40px;
  background-color: rgb(51, 224, 51);
  outline: 0 none;
  border: 0 none;
  cursor: pointer;
  color: #fff;
  border-radius: 25px;
  font-size: 16px;
}

.filter {
  width: 100%;
  margin-top: 15px;
  line-height: 40px;
  font-size: 16px;
  border-radius: 25px;
  border: 1px solid #e0e3e4;
  text-indent: 15px;
}
